import React, { useState } from "react";
import { Plus, Trash2 } from "react-feather";
import DynamicAddOn from "./DynamicAddOn";

function DynamicMeal() {
    const [inputList, setInputList] = useState([{ type: "", time: "", name: "", image: "", day: "", add_on: "", add_on_price: "" }]);

    // handle input change
    const handleInputChange = (e, index) => {
        const { name, value } = e.target;
        const list = [...inputList];
        list[index][name] = value;
        setInputList(list);
    };

    // handle click event of the Remove button
    const handleRemoveClick = index => {
        const list = [...inputList];
        list.splice(index, 1);
        setInputList(list);
    };

    // handle click event of the Add button
    const handleAddClick = () => {
        setInputList([...inputList, { type: "", time: "", name: "", image: "", day: "", add_on: "", add_on_price: "" }]);
    };

    return (
        <>
            {inputList.map((x, i) => {
                return (
                    <>
                        <div className="row mg-t-10 mx-1 px-1">
                            <div className="col-sm-4 mg-t-20 mg-sm-t-30">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                    Available as
                            </label>
                                <p className="mg-b-0">
                                    <select className="mg-b-0" onChange={handleInputChange} value={inputList.lunch}>
                                        <option name="time" value="Lunch">Lunch</option>
                                        <option name="time" value="Dinner">Dinner</option>
                                    </select>
                                </p>
                             
                            </div>
                            <div className="col-sm-4 mg-t-20 mg-sm-t-30">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                    Meal type
                            </label>
                                <p className="mg-b-0">
                                    <select className="mg-b-0">
                                        <option name="Veg" value="Veg">Veg</option>
                                        <option name="non-veg" value="non-veg">Non-Veg</option>
                                    </select>
                                </p>
                            </div>

                        </div>

                        <div className="row row-sm px-1">
                            <div className="col-6 col-sm-4 ">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                    Meal Name
                                </label>
                                <p className="mg-b-0">
                                    <input

                                        name="restaurant_name"


                                    />
                                </p>
                            </div>
                            <div className="col-6 col-sm-4 ">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                    Image
                                </label>
                                <div className="custom-file px-2">
                                    <input
                                        type="file"
                                        className="custom-file-input"
                                        id="customFile"
                                    />
                                    <label className="custom-file-label" for="customFile">
                                        Choose file
                    </label>
                                </div>
                            </div>
                        </div>

                        <div className="row row-sm px-1">
                            <div className="col-10 col-sm-8 mg-sm-t-10">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                    Description
                    </label>
                                <p className="mg-b-0">
                                    <textarea

                                        name="phone"

                                    />
                                </p>
                            </div>
                        </div>
                        <DynamicAddOn />
                        <div className="row row-sm px-1 mg-b-10">
                            <div className="col-6 col-sm-4 mg-t-20 mg-sm-t-30">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                </label>
                                <p className="mg-b-0">
                                    <button
                                        className="btn btn-sm btn-primary"
                                    > Save and next</button>
                                </p>
                            </div>
                            <div className="col-6 col-sm-4 mg-t-20 mg-sm-t-30">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                </label>
                                <p className="mg-b-0">
                                    <button
                                        onClick={() => {
                                            window.location.href="restaurant-dashboard"
                                        }}
                                        className="btn btn-sm btn-danger"
                                    >Cancel</button>
                                </p>
                            </div>
                            <div className="col-6 col-sm-4 mg-t-20 mg-sm-t-30">
                                {inputList.length !== 1 && <button
                                    className="btn btn-sm btn-danger mg-sm-t-10"
                                    onClick={() => handleRemoveClick(i)}>
                                    <Trash2/>
                                    </button>}
                                {inputList.length - 1 === i && <button
                                    className="btn btn-sm btn-primary mg-sm-t-10"
                                    onClick={handleAddClick}>
                                    Add More Meal
                                    </button>}
                            </div>
                        </div>



                      
                    </>
                );
            })}

        </>
    );
}

export default DynamicMeal;