import React, { useState } from "react";
import { Plus, Trash2 } from "react-feather";

function DynamicAddOn() {
    const [inputList, setInputList] = useState([{ add_on: "", add_on_price: "" }]);

    // handle input change
    const handleInputChange = (e, index) => {
        const { name, value } = e.target;
        const list = [...inputList];
        list[index][name] = value;
        setInputList(list);
    };

    // handle click event of the Remove button
    const handleRemoveClick = index => {
        const list = [...inputList];
        list.splice(index, 1);
        setInputList(list);
    };

    // handle click event of the Add button
    const handleAddClick = () => {
        setInputList([...inputList, { add_on: "", add_on_price: "" }]);
    };

    return (
        <>
            {inputList.map((x, i) => {
                return (
                    <>
                        <div className="row row-sm px-1">
                            <div className="col-sm-4 mg-t-20 mg-sm-t-30">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                    Add On
                                </label>
                                <p className="mg-b-0">
                                    <input
                                        name="add_on"
                                        value={inputList.add_on}
                                        onChange={handleInputChange}
                                    />
                                </p>
                            </div>
                            <div className="col-sm-4 mg-t-20 mg-sm-t-30">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                    price
                                </label>
                                <p className="mg-b-0">
                                    <input
                                        value={inputList.add_on_price}
                                        onChange={handleInputChange}
                                        name="add_on_price"

                                    />
                                </p>
                            </div>
                            <div className="col-sm-4 mg-t-20 mg-sm-t-30">
                            {inputList.length !== 1 && <button
                                className="btn btn-sm btn-danger mg-t-20 mx-2"
                                    onClick={() => handleRemoveClick(i)}>
                                    <Trash2/>
                                </button>}
                                {inputList.length - 1 === i && <button className="btn btn-sm btn-primary mg-t-20" onClick={handleAddClick}>
                                    <Plus/>
                            </button>}
                            </div>
                        </div>
                    </>
                );
            })}

        </>
    );
}

export default DynamicAddOn;