import axios from "axios";


export const getRestaurants=()=>{
    let Restaurant=axios
      .get("/api/restaurant/")
      .then((res) => {
        let Restaurant;
        Restaurant=res.data
        return Restaurant
        
      })
      .catch((err) => {
        console.log(err);
      });
      return Restaurant 
}

export const getRestaurantbyID=(idofclient)=>{
  let Restaurant=axios
      .get("/api/restaurant/"+idofclient)
      .then((res) => {
        let Restaurant;
        Restaurant=res.data
        return Restaurant
        
      })
      .catch((err) => {
        console.log(err);
      });
      return Restaurant 
}
export const addRestaurant=(newRestaurant)=>{
  axios.post('/api/restaurant/',newRestaurant).then((res)=>{
    console.log(res.data)
  }).catch((err)=>{
    console.log(err)
  })
}
export const deleteMultipleRestaurant=(arrRestaurant)=>{
  axios.delete('/api/restaurant/',arrRestaurant).then((res)=>{
    console.log(res.data);
  }).catch((err)=>{
    console.log(err);
  })
}
export const deleteRestaurant=(idofclient)=>{
  axios.delete('/api/restaurant/'+idofclient).then((res)=>{
    console.log(res);
  }).catch((err)=>{
    console.log(err);
  })
}

export const updateRestaurant=(idofclient,newRestaurant)=>{
  axios.post('/api/restaurant/'+idofclient,newRestaurant).then((res)=>{
    console.log(res.data)
  }).catch((err)=>{
    console.log(err)
  })
}
