import React, { Component } from "react";
import SideNav from "../components/SideNav";
import TopNav from "../components/TopNav";
import ErrorBoundary from "../components/ErrorBoundary";
import AddUserForm from "../components/AddUserForm";
import { MdRestaurant, } from "react-icons/md";
import DummyRestaurantTable from "../components/DummyRestaurantTable";
import {getRestaurants} from '../../controllers/RestaurantController'

const Clt = getRestaurants()
export default class RestaurantScreen extends Component {
  constructor(props) {
    super(props);
    this.populateAddForm = this.populateAddForm.bind(this);
    this.state = {
      err: null,
      isLoaded: false,
      Restaurants:[],
      active:[],
      inactive:[],
      addState: false,
      name: "",
    };
  }
  populateAddForm() {
    this.setState({ addState: true });
  }
  componentDidMount() {
    Clt.then((data) => {
      let active = data.filter(function (e) {
        return e.status === 'Active'
      })
      let inactive = data.filter(function (e) {
        return e.status === 'Inactive'
      })
      this.setState({ isLoaded: true, Restaurants: data, active: active, inactive: inactive });
    })
      .catch((err) => {
        console.log(err);
      });
  }
  render() {
    const { err, addState,Restaurants,active,inactive } = this.state;
    if (err) {
      return <div className="contact-wrapper">Error:{err.message}</div>;
    } else {
      return (
        <>
          <TopNav />
          <div className="contact-wrapper">
            <SideNav />
            <div
              style={{
                position: "absolute",
                left: 157,
                top: 20,
                width: 1200,
                overflowX: "hidden",
              }}
            >
              <div className="row mb-4">
                <div
                  className="col-sm-3 ml-5 col-lg-3 px-0"
                  style={{ backgroundColor: "aliceblue" }}
                >
                  <div
                    className="card card-body px-1"
                    style={{ backgroundColor: "blue" }}
                  >
                    <div className="row">
                      <div className="col-sm-8">
                        <h6 className="tx-normal tx-11 tx-spacing-1 tx-white tx-semibold mg-b-1">
                          All Restaurant
                        </h6>
                        <h6 className="tx-40 tx-spacing-1 tx-white tx-semibold mg-b-5">
                          {Restaurants.length}
                        </h6>
                      </div>
                      <div className="col-sm-4">
                        <MdRestaurant size={40} color="#FFF" />
                      </div>
                    </div>

                  </div>

                </div>
                {/* All Users */}
                <div
                  className="col-sm-3 ml-5 col-lg-3 px-0"
                  style={{ backgroundColor: "teal" }}
                >
                  <div
                    className="card card-body px-1"
                    style={{ backgroundColor: "green" }}
                  >
                    <div className="row">
                      <div className="col-sm-8">
                        <h6 className="tx-normal tx-11 tx-spacing-1 tx-white tx-semibold mg-b-1">
                          Active Restaurant
                        </h6>
                        <h6 className="tx-40 tx-spacing-1 tx-white tx-semibold mg-b-5">
                          {active.length}
                        </h6>
                      </div>
                      <div className="col-sm-4">
                        <MdRestaurant size={40} color="#FFF" />
                      </div>
                    </div>
                  </div>
                </div>
                {/*Active Users  */}
                <div
                  className="col-sm-3 ml-5 col-lg-3 px-0"
                  style={{ backgroundColor: "red" }}
                >
                  <div
                    className="card card-body px-1"
                    style={{ backgroundColor: "#ff0000" }}
                  >
                    <div className="row">
                      <div className="col-sm-8">
                        <h6 className="tx-normal tx-11 tx-spacing-1 tx-white tx-semibold mg-b-1">
                          Inactive Restaurant
                        </h6>
                        <h2 className="tx-40 tx-spacing-1 tx-white tx-semibold mg-b-5">
                          {inactive.length}
                        </h2>
                      </div>
                      <div className="col-sm-4">
                        <MdRestaurant size={40} color="white" />
                      </div>
                    </div>
                  </div>
                </div>
                {/* Inactive Users */}
              </div>

              <ErrorBoundary>
                {addState ? (
                  <AddUserForm />
                ) : (
                  <DummyRestaurantTable isLoaded />
                )}
              </ErrorBoundary>
            </div>
          </div>
        </>
      );
    }
  }
}
